body {
  margin: 15px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

ul,
li {
  list-style: none;
  padding: 0;
}

ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

/* about */

#about--image {
  width: 150px; /* Adjust the width to your preferred size */
  height: 150px; /* Ensure the height matches the width to make it a circle */
  border-radius: 50%;
  object-fit: cover;
}

.about {
  margin-top: 80px;
}

.about--text {
  text-align: left;
  margin-top: 30px;
}

.about--text h1 {
  font-size: 25px;
}

.about--text h2 {
  font-size: 20px;
}

/* skills */

.skill {
  display: flex;
  flex-direction: column;
}

.skills--container {
  display: flex;
  margin-top: 40px;
}

.skills--icon {
  font-size: 40px;
}
